import * as React from 'react';
import { useParams } from 'react-router-dom';
import {
  FullScreen,
  BodyScreen,
  ContentScreen,
  AiderScreen,
} from '@/layouts/page-layout';
import {
  Box,
  ImageList,
  Button,
  Typography,
  ImageListItem,
  ImageListItemBar,
} from '@mui/material';
import { MediaNormal } from '@/components/app/media';
import { SpecialBox } from '@/components/public/special-box';
import {
  useSearchShadowsPaginatedQuery,
  SearchShadowsPaginatedQuery,
} from '@/schema';
import { useNavigate } from 'react-router-dom';
import { Placeholder } from '@/components/base/placeholder';

export const Search: React.FC = () => {
  const { word } = useParams();
  const navigate = useNavigate();

  const { data, loading, error, fetchMore } = useSearchShadowsPaginatedQuery({
    variables: {
      query: {
        last: 16,
      },
      word: word,
    },
  });

  const loadMore = () => {
    fetchMore({
      variables: {
        query: {
          last: 8,
          before: data?.search_shadows_paginated?.pageInfo?.endCursor,
        },
        word: word,
      },
      updateQuery: (
        previousQueryResult: SearchShadowsPaginatedQuery,
        { fetchMoreResult }
      ) => {
        if (fetchMoreResult?.search_shadows_paginated.edges) {
          const { edges, pageInfo, totalCount } =
            fetchMoreResult.search_shadows_paginated;
          return {
            search_shadows_paginated: {
              pageInfo,
              totalCount,
              edges: [
                ...(previousQueryResult.search_shadows_paginated.edges || []),
                ...edges,
              ],
              __typename:
                previousQueryResult.search_shadows_paginated.__typename,
            },
          };
        }

        return previousQueryResult;
      },
    });
  };

  return (
    <FullScreen>
      <BodyScreen>
        <ContentScreen
          sx={(theme) => ({
            margin: '0 auto',
            padding: theme.spacing(8),
            background: theme.palette.background.paper,
          })}
        >
          {data?.search_shadows_paginated?.edges?.length ? (
            <Box
              sx={(theme) => ({
                display: 'grid',
                gridTemplateColumns: 'repeat(4, 1fr)',
                gridGap: theme.spacing(1, 1),
                gridAutoFlow: 'row dense',
              })}
            >
              {data?.search_shadows_paginated?.edges?.map((edge: any) => {
                return (
                  <Box
                    key={edge.cursor}
                    sx={(theme) => ({
                      gridRow: '1 span',
                      gridColumn: '1 span',
                    })}
                  >
                    <MediaNormal
                      {...edge.node}
                      onClickRoot={() => navigate(`/shadow/${edge.node.id}`)}
                      onClickAuthor={() =>
                        navigate(`/profile/${edge.node.author.username}`)
                      }
                    />
                  </Box>
                );
              })}
            </Box>
          ) : (
            <SpecialBox
              loading={loading}
              error={!!error}
              placeholder
              placeholderSubtitle="暂无搜索结果"
              loadingNode={
                <ImageList rowHeight="auto" cols={4}>
                  {[0, 0, 0, 0, 0, 0, 0, 0].map((_, index: number) => {
                    return (
                      <ImageListItem cols={1} key={index}>
                        <ImageListItemBar>
                          <MediaNormal loading />
                        </ImageListItemBar>
                      </ImageListItem>
                    );
                  })}
                </ImageList>
              }
            />
          )}

          {data?.search_shadows_paginated?.pageInfo?.hasNextPage && (
            <Box
              sx={(theme) => ({
                padding: theme.spacing(2),
                textAlign: 'center',
              })}
            >
              <Button onClick={loadMore} disabled={loading}>
                <Typography color="textSecondary" variant="caption">
                  {loading ? '加载中...' : '加载更多'}
                </Typography>
              </Button>
            </Box>
          )}
        </ContentScreen>
        <AiderScreen
          sticky
          sx={(theme) => ({
            backgroundColor: theme.palette.background.paper,
            width: '100%',
            padding: theme.spacing(2, 2),
            borderLeft: '1px solid',
            borderColor: theme.palette.divider,
          })}
        >
          {/* <Sort /> */}
          {/* <Filter /> */}
          <Placeholder subtitle="更多功能，敬请期待" />
        </AiderScreen>
      </BodyScreen>
    </FullScreen>
  );
};

export default Search;
